/*                        */
/* macro sidebar settings */
/*                        */

/* parent tag */
.sidebar {
  position: absolute;
  height: 100vh;
  width: 0;
  top: 0;
  left: 0;
  z-index: 7;

  /* color/styling */
  background-color: rgba(11, 3, 45, 0.95);
  -webkit-backdrop-filter: blur(3px);
          backdrop-filter: blur(3px);

  box-shadow: -9px 0px 18px black inset;

  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 32px;
  padding-left: 0px;
  padding-right: 0px;
  padding-bottom: 32px;
}

.sidebar a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 15px;
  color: white !important;
  display: block;
  transition: 0.3s;
}
.sidebar a:hover {
  color: orange !important;
}

.sidebar .closebtn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 20px;
  margin-left: 50px;
}

#sidebarLogo {
  position: absolute;
  padding-bottom: 32px;
  float: left;
  bottom: 0%;
  width: auto;
  color: white;
}

/* On smaller screens, where height is less than 450px, change the style of the sidenav (less padding and a smaller font size) */
@media screen and (max-height: 450px) {
  .sidebar {
    padding-top: 15px;
  }
  .sidebar a {
    font-size: 18px;
  }
}

.loader-container {
  position: absolute;
  width: 100vw;
  height: 100vh;
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
  background-color: #f0f0f0b2;
  z-index: 10;
}
.loader {
  margin: auto;
  position: relative;
  top: 40vh;
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

